.loader {
  width: 100%;
  justify-content: center;
  align-items: center;
  margin: 20px;
  display: flex;
  position: fixed;
  top: 15%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.loader:after, .loader:before {
  content: "";
  width: 28px;
  height: 38px;
  background-color: #f6ecec;
  background-image: radial-gradient(14px, #0d161b 80%, rgba(0, 0, 0, 0) 0);
  background-repeat: no-repeat;
  border: 2px solid #c2bcbc;
  border-radius: 50%;
  animation: eyeMove 10s infinite, blink 10s infinite;
  display: inline-block;
}

@keyframes eyeMove {
  0%, 10% {
    background-position: 0 0;
  }

  13%, 40% {
    background-position: -15px 0;
  }

  43%, 70% {
    background-position: 15px 0;
  }

  73%, 90% {
    background-position: 0 15px;
  }

  93%, 100% {
    background-position: 0 0;
  }
}

@keyframes blink {
  0%, 10%, 12%, 20%, 22%, 40%, 42%, 60%, 62%, 70%, 72%, 90%, 92%, 98%, 100% {
    height: 48px;
  }

  11%, 21%, 41%, 61%, 71%, 91%, 99% {
    height: 18px;
  }
}

.loader-text {
  color: #a59a9a;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
  margin: 0 10px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 16px;
}

.loader-line {
  height: 4px;
  width: 0%;
  background: linear-gradient(to right, red, #ff0, #0f0);
  animation: lineGrow 5s linear infinite;
  display: block;
}

@keyframes lineGrow {
  0% {
    width: 0%;
  }

  50% {
    width: 50%;
  }

  100% {
    width: 100%;
  }
}

.error {
  color: red;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, .5);
  justify-content: center;
  align-items: center;
  animation: errorAnimation 6s infinite;
  display: none;
  position: fixed;
  top: 15%;
  left: 33%;
  transform: translate(-50%, -50%);
}

@keyframes errorAnimation {
  0% {
    color: red;
    transform: scale(1);
  }

  50% {
    color: red;
    transform: scale(.9);
  }

  100% {
    color: red;
    transform: scale(1);
  }
}

